body {
  margin: 0;
  background-color: #121212;
  font-family: 'Noto Sans', 'Noto Sans Display', 'Quicksand', sans-serif;
}

.settings-container {
  padding: 30px;
  max-width: 290px;
  margin: 30px auto;
  color: #e0e0e0;

  background-color: rgba(30, 30, 30, 0.315); /* полупрозрачный фон */
  backdrop-filter: blur(30px);             /* размытие фона */
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.8),
              0 0 2px rgba(255, 255, 255, 0.6);

  overflow-y: auto;
  max-height: 80vh;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.settings-container::-webkit-scrollbar {
  display: none;
}



.settings-heading {
  margin-bottom: 30px;
  font-size: 1.8rem;
  text-align: center;
}

.settings-section-title {
  font-size: 1.2rem;
  margin: -5px 0 10px;
  color: #f0f0f0;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.settings-card {
  padding: 10px;
  border-radius: 16px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.settings-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  font-size: 1rem;
  background-color: rgba(30, 30, 30, 0.6); /* полупрозрачный фон */
  backdrop-filter: blur(8px);             /* размытие фона */
  color: #e0e0e0;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  cursor: pointer;
  transition: background-color 0.2s;
  letter-spacing: 1px;
}

.settings-button:hover {
  background-color: #2a2a2a;
}

.settings-button-content {
  display: flex;
  align-items: center;
  gap: 10px;
}

.settings-icon {
  font-size: 1.2rem;
  color: #6FCF00; /* яркий зелёный */
}

.arrow-icon {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.4);
}
