body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #121212;
  height: 150vh;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px 40px;
  box-sizing: border-box;
  overflow: hidden;
}

.week-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease-out;
  height: 100%;
  width: 100%;
  gap: 90px;
}

.week-block {
  width: 100%;
  max-width: 500px;
  border-radius: 16px;
  background-color: #1a1a1a; /* тёмная карточка */
  padding: 20px;
  margin: 10px 0;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.week-title {
  font-size: 1.5em;
  color: #e0e0e0;
  margin-bottom: 15px;
}

.week-days {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.day {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin: 5px 0;
  padding-bottom: 0px;
  border-bottom: 1px solid #2a2a2a;
}

.day-name {
  font-weight: bold;
  color: #bbbbbb;
}

.day-date {
  margin-top: 5px;
  text-align: left;
  width: 100%;
  font-size: 1.2em;
  color: #999999;
}

.day.current-day {
    margin-top: -5px !important;
  width: 100%; /* Растягиваем на всю ширину родителя */
  background: linear-gradient(90deg, #202020, #1e1e1e);
  border-left: 4px solid #b8b8b8; /* Левое выделение вместо полной рамки */
  padding: 10px; /* Визуальный отступ внутри блока */
  box-sizing: border-box;
}

