body {
  margin: 0;
  background-color: #121212;
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Text", "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #e0e0e0;
  letter-spacing: 0.2px;
  line-height: 1.5;
}

.otes-wrapper {
  padding: 0px 40px;
  max-width: 1200px;
  margin: 0 auto;
  letter-spacing: 1px;
  overflow: hidden;
}

.otes-title {
  text-align: left; /* выравнивание по левому краю */
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #ffffff;
  letter-spacing: 1.5px;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
  padding-left: 0; /* убираем центрирование */
}

.otes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 14px;
}

.otes-card {
  background-color: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.7),
              0 0 1px rgba(255, 255, 255, 0.3);
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.otes-card-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #f0f0f0;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  justify-content: center;  /* центрируем по горизонтали */
  text-align: center;       /* на всякий случай */
  gap: 10px;
}

.otes-value {
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 4px;
}

.otes-green {
  color: #f0f0f0;
}

.otes-yellow {
  color: #FFD54F;
}

.otes-blue {
  color: #4FC3F7;
}

.otes-red {
  color: #EF5350;
}

.otes-progress {
  width: 100%;
  height: 10px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  margin-top: 10px;
}

.otes-progress-bar {
  height: 100%;
  background-color: #84e000;
  border-radius: 8px;
  transition: width 0.4s ease-in-out;
}

.otes-update-button {
  margin: 40px auto 0;
  display: block;
  padding: 14px 24px;
  font-size: 1rem;
  background-color: #6FCF00;
  color: #121212;
  font-weight: bold;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background-color 0.3s;
  box-shadow: 0 4px 20px rgba(111, 207, 0, 0.3);
}

.otes-update-button:hover {
  background-color: #84e000;
}

.otes-status-badge {
  margin-top: 12px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85rem;
  font-weight: bold;
  padding: 6px 10px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.08);
  width: fit-content;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.fade-in {
  animation: fadeIn 0.8s ease both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.otes-quote {
  text-align: center;
  margin-top: 50px;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.4);
  font-style: italic;
  letter-spacing: 0.5px;
}

.otes-inputs {
  margin-bottom: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
}

.otes-input-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.otes-input-group label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.otes-input-group input {
  padding: 10px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.07);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  font-size: 1rem;
  outline: none;
  transition: 0.2s;
}

.otes-input-group input:focus {
  border-color: #6FCF00;
  background-color: rgba(255, 255, 255, 0.1);
}

.otes-date-range {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  font-size: 1rem;
  margin-bottom: 15px;
  background-color: rgba(255,255,255,0.05);
  padding: 10px 10px;
  border-radius: 12px;
  color: #ddd;
  backdrop-filter: blur(10px);
  box-shadow: inset 0 0 5px rgba(255,255,255,0.05);
}

.otes-date-range button {
  background: none;
  border: none;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  padding: 4px 10px;
  border-radius: 8px;
  transition: 0.2s;
}

.otes-nav-buttons {
  display: flex;
  gap: 8px;
}

.otes-nav-buttons button {
  background: none;
  color: #fff;
  font-size: 18px;
  border: none;
  cursor: pointer;
  padding: 4px 10px;
  border-radius: 8px;
  transition: 0.2s;
}


.otes-input-card .otes-inputs {
  margin: 20px 0 0;
}

.otes-input-group {
  display: flex;
  flex-direction: column;
  margin-top: 12px;
}

.otes-input-group label {
  font-size: 14px;
  margin-bottom: 4px;
  color: #666;
}

.otes-input-group input {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 14px;
}

.otes-progress-info {
  margin-top: 10px;
  font-size: 14px;
}

/* Блокирует выделение текста и контекстное меню */
.otes-wrapper {
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  pointer-events: auto;
  -webkit-touch-callout: none; /* iOS Safari */
}

.otes-wrapper * {
  user-select: none !important;
  -webkit-user-select: none !important;
  -ms-user-select: none !important;
  -moz-user-select: none !important;
  -webkit-touch-callout: none !important;
}

.otes-icon-corner {
  position: absolute;
  bottom: 8px;
  right: 8px;
  font-size: 28px;
  color: #ccc;
}

.otes-slide-panel {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.4s ease;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: -10px;
}

.otes-slide-panel.open {
  max-height: 300px; /* любое подходящее значение */
  opacity: 1;
  transform: translateY(0);
}

.unit {
  font-size: 0.75em;
  font-weight: normal;
  opacity: 0.8;
}


/* 💨 Свайп влево: следующий месяц → контент заходит справа */
.animate-slide-left {
  animation: slideFromRight 0.5s ease forwards;
}

/* 💨 Свайп вправо: предыдущий месяц → контент заходит слева */
.animate-slide-right {
  animation: slideFromLeft 0.5s ease forwards;
}

@keyframes slideFromRight {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes slideFromLeft {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

